import  React from 'react'

import './directory-menu.styles.scss'
import {MenuItem} from '../menu-item/menu-item.component'

class DirectoryMenu extends React.Component{

    constructor() {
        super()
        this.state = {
            section: [
                {
                    'title': 'Supreme',
                    'image': 'img/supreme.jpg',
                    'id': 1,
                },
                {
                    'title': 'Off White',
                    'image': 'img/off-white.jpg',
                    'id': 2,
                },
                {
                    'title': 'Stussy',
                    'image': 'img/stussy.jpg',
                    'id': 3,
                },
                {
                    'title': 'Champion',
                    'image': 'img/champion.jpg',
                    'id': 4,
                    'size' : 'large'
                },
                {
                    'title': 'Thrasher',
                    'image': 'img/thrasher.jpg',
                    'id': 5,
                    'size': 'large'
                },
            ]
        }
    }

    render() {
        return(
            <div className="directory-menu">
                {
                    this.state.section.map(({title, image, id, size}) => {
                        return <MenuItem key={id} image={image} title={title} size={size}/>
                    })
                }
            </div>
        )
    }
}

export default DirectoryMenu